<template>
  <v-card class="ma-md-6 ma-sm-3">
    <v-card-title class="py-2">
      <v-icon class="mr-3">mdi-account-setting</v-icon>
      Pengaturan Akun
    </v-card-title>
    <v-divider></v-divider>
    <v-form v-model="valid" ref="form">
      <v-card-text>
        <v-text-field
          v-model="dataAkun.nama"
          label="Username"
          outlined
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-text-field
          v-model="dataAkun.kepala"
          label="Password lama"
          outlined
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-text-field
          v-model="dataAkun.hp"
          label="Password baru"
          outlined
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-text-field
          v-model="dataAkun.alamat"
          label="Ulangi password baru"
          outlined
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-text-field
          v-model="dataAkun.prov"
          :items="item"
          label="E-mail"
          outlined
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-text-field
          v-model="dataAkun.kab"
          :items="item"
          label="No Hp/WA"
          outlined
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          :disabled="!valid"
          color="primary"
          @click.prevent="validate"
          type="submit"
          depressed>
          simpan
        </v-btn>
      </v-card-actions>
    </v-form>
  </v-card>
</template>

<script>
  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        dataAkun: {
          nama: '',
          kepala: '',
          hp: '',
          alamat: '',
          prov: '',
          kab: ''
        },
        items: ['foo', 'bar', 'zee'],
        requiredRule: [v => !!v || 'Data harus diisi']
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          console.log(this.dataAkun)
        }
      }
    }
  }
</script>
